// Crear nodos vacos para cada roadmap (solo el nodo central)
const createEmptyNodes = (title, icon) => [
  {
    id: 'central',
    type: 'custom',
    position: { x: 0, y: 0 },
    data: {
      label: title,
      description: `Mapa mental de ${title}`,
      icon: icon,
      nodeType: 'title'
    }
  }
];

