import React from 'react';
import { Link } from 'react-router-dom';
import { FiArrowLeft, FiMail, FiShield } from 'react-icons/fi';

export default function PrivacyPolicy() {
  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <div className="mb-8">
          <Link to="/" className="inline-flex items-center text-blue-600 hover:text-blue-800 mb-4">
            <FiArrowLeft className="mr-2" />
            Volver al inicio
          </Link>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Poltica de Privacidad</h1>
          <p className="text-gray-600">ltima actualizacin: Enero 2025</p>
        </div>

        {/* Contenido */}
        <div className="bg-white rounded-xl shadow-lg border border-gray-100 p-6 sm:p-8">
          <div className="prose prose-lg max-w-none">
            <h2>1. Informacin General</h2>
            <p>
              MetalRoadmap ("nosotros", "nuestro", "la aplicacin") es una plataforma educativa especializada en ingeniera metalrgica que permite a los usuarios crear y gestionar roadmaps de aprendizaje personalizados.
            </p>

            <h2>2. Informacin que Recolectamos</h2>
            
            <h3>2.1 Informacin de Registro</h3>
            <ul>
              <li><strong>Direccin de correo electrnico</strong>: Para crear y gestionar tu cuenta</li>
              <li><strong>Nombre</strong>: Opcional, obtenido desde LinkedIn o proporcionado por el usuario</li>
              <li><strong>Contrasea</strong>: Solo para registro con email (no almacenamos contraseas en texto plano)</li>
            </ul>

            <h3>2.2 Informacin de LinkedIn (Opcional)</h3>
            <p>Si eliges iniciar sesin con LinkedIn, podemos recibir:</p>
            <ul>
              <li>Nombre y apellido</li>
              <li>Direccin de correo electrnico</li>
              <li>Foto de perfil (si est disponible)</li>
              <li>Informacin bsica del perfil pblico</li>
            </ul>

            <h3>2.3 Contenido del Usuario</h3>
            <ul>
              <li><strong>Roadmaps creados</strong>: Ttulos, descripciones, emojis</li>
              <li><strong>Conceptos y nodos</strong>: Informacin educativa que agregas</li>
              <li><strong>Recursos</strong>: Enlaces y referencias que compartes</li>
              <li><strong>Metadatos</strong>: Fechas de creacin, modificaciones</li>
            </ul>

            <h2>3. Cmo Usamos tu Informacin</h2>
            
            <h3>3.1 Propsitos Principales</h3>
            <ul>
              <li><strong>Autenticacin</strong>: Verificar tu identidad y mantener tu sesin</li>
              <li><strong>Funcionalidad</strong>: Permitirte crear, editar y gestionar roadmaps</li>
              <li><strong>Mejoras</strong>: Analizar el uso para mejorar la aplicacin</li>
              <li><strong>Comunicacin</strong>: Enviar notificaciones importantes sobre tu cuenta</li>
            </ul>

            <h3>3.2 Anlisis y Mejoras</h3>
            <ul>
              <li>Uso de datos agregados para mejorar la experiencia del usuario</li>
              <li>No vendemos, alquilamos o compartimos datos personales con terceros</li>
            </ul>

            <h2>4. Almacenamiento y Seguridad</h2>
            
            <h3>4.1 Base de Datos</h3>
            <ul>
              <li>Utilizamos Supabase (PostgreSQL) para almacenar datos</li>
              <li>Todos los datos estn protegidos con Row Level Security (RLS)</li>
              <li>Cada usuario solo puede acceder a sus propios datos</li>
            </ul>

            <h3>4.2 Seguridad</h3>
            <ul>
              <li>Conexiones encriptadas (HTTPS)</li>
              <li>Autenticacin segura</li>
              <li>Contraseas hasheadas (cuando aplica)</li>
              <li>Acceso restringido a datos personales</li>
            </ul>

            <h2>5. Compartir Informacin</h2>
            
            <h3>5.1 No Vendemos Datos</h3>
            <ul>
              <li>No vendemos, alquilamos o comercializamos tus datos personales</li>
              <li>No compartimos informacin con anunciantes</li>
            </ul>

            <h3>5.2 Proveedores de Servicios</h3>
            <p>Podemos compartir datos con:</p>
            <ul>
              <li><strong>Supabase</strong>: Para almacenamiento y autenticacin</li>
              <li><strong>Vercel</strong>: Para hosting de la aplicacin</li>
              <li><strong>LinkedIn</strong>: Solo si usas login con LinkedIn</li>
            </ul>

            <h2>6. Tus Derechos</h2>
            
            <h3>6.1 Acceso y Control</h3>
            <ul>
              <li><strong>Ver tus datos</strong>: Puedes ver toda la informacin que tenemos sobre ti</li>
              <li><strong>Editar</strong>: Puedes actualizar tu informacin de perfil</li>
              <li><strong>Eliminar</strong>: Puedes eliminar tu cuenta y todos tus datos</li>
              <li><strong>Exportar</strong>: Puedes solicitar una copia de tus datos</li>
            </ul>

            <h3>6.2 Eliminacin de Datos</h3>
            <ul>
              <li>Al eliminar tu cuenta, todos tus roadmaps y datos se eliminan permanentemente</li>
              <li>Los datos eliminados no se pueden recuperar</li>
            </ul>

            <h2>7. Cookies y Tecnologas de Seguimiento</h2>
            
            <h3>7.1 Cookies Necesarias</h3>
            <ul>
              <li><strong>Sesin</strong>: Para mantener tu sesin activa</li>
              <li><strong>Preferencias</strong>: Para recordar tus configuraciones</li>
              <li><strong>Seguridad</strong>: Para proteger contra ataques</li>
            </ul>

            <h3>7.2 No Usamos</h3>
            <ul>
              <li>Cookies de publicidad</li>
              <li>Seguimiento de terceros</li>
              <li>Anlisis invasivo</li>
            </ul>

            <h2>8. Menores de Edad</h2>
            <ul>
              <li>Nuestra aplicacin no est dirigida a menores de 13 aos</li>
              <li>No recolectamos intencionalmente informacin de menores de 13 aos</li>
              <li>Si eres padre y crees que tu hijo nos ha proporcionado informacin, contctanos</li>
            </ul>

            <h2>9. Cambios a esta Poltica</h2>
            <ul>
              <li>Notificaremos cambios importantes por email</li>
              <li>La fecha de "ltima actualizacin" se modificar</li>
              <li>El uso continuado significa aceptacin de los cambios</li>
            </ul>

            <h2>10. Contacto</h2>
            
            <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-6">
              <h3 className="flex items-center text-blue-800 mb-2">
                <FiMail className="mr-2" />
                Para preguntas sobre privacidad:
              </h3>
              <ul className="text-blue-700">
                <li><strong>Email</strong>: [tu-email@dominio.com]</li>
                <li><strong>Sitio web</strong>: [tu-sitio-web.com/privacy]</li>
              </ul>
            </div>

            <div className="bg-green-50 border border-green-200 rounded-lg p-4">
              <h3 className="flex items-center text-green-800 mb-2">
                <FiShield className="mr-2" />
                Para solicitudes de datos:
              </h3>
              <ul className="text-green-700">
                <li><strong>Acceso a datos</strong>: Solicita una copia de tus datos</li>
                <li><strong>Eliminacin</strong>: Solicita eliminar tu cuenta y datos</li>
                <li><strong>Correccin</strong>: Solicita corregir informacin incorrecta</li>
              </ul>
            </div>

            <h2>11. Jurisdiccin</h2>
            <p>
              Esta poltica se rige por las leyes de [tu pas/regin] y cumple con:
            </p>
            <ul>
              <li>GDPR (Europa)</li>
              <li>CCPA (California)</li>
              <li>LGPD (Brasil)</li>
              <li>Otras regulaciones aplicables</li>
            </ul>

            <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mt-8">
              <p className="text-yellow-800 text-sm">
                <strong>Nota</strong>: Esta poltica de privacidad es un template. Debes personalizarla con:
              </p>
              <ul className="text-yellow-700 text-sm mt-2">
                <li>Tu informacin de contacto real</li>
                <li>Tu jurisdiccin legal</li>
                <li>Cualquier prctica especfica de tu aplicacin</li>
                <li>Revisin legal si es necesario para tu caso de uso</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 